import React, { Component } from 'react'
import { Mutation, Query } from 'react-apollo'
import Form from './styles/Form'
import Router from 'next/router'

import gql from 'graphql-tag'
import styled from 'styled-components'
import Error from './ErrorMessage'
import SickButton from './styles/SickButton'
import User, { CURRENT_USER_QUERY } from './User'

const Inner = styled.div`
  max-width: ${props => props.theme.innerWidth};
  margin: 0 auto;
  padding: 2rem;
  .dates {
    font-family: montserrat, sans-serif;
    text-transform: uppercase;
    opacity: 0.5;
    &:focus {
      opacity: 1;
    }
    &:active {
      opacity: 1;
    }
  }
`

const UPDATE_USER_MUTATION = gql`
  mutation UPDATE_USER_MUTATION(
    $id: ID!
    $name: String
    $businessName: String!
    $email: String!
  ) {
    updateUser(
      id: $id
      name: $name
      businessName: $businessName
      email: $email
    ) {
      id
      name
      businessName
      email
    }
  }
`

class UpdateUserProfile extends Component {
  state = { name: '', businessName: '', email: '' }
  handleChange = e => {
    const { name, type, value } = e.target
    const val = type === 'number' ? parseFloat(value) : value
    this.setState({ [name]: val })
  }
  updateUser = async (e, updateUserMutation) => {
    e.preventDefault()
    console.log('Updating User!')
    console.log(this.state)
    const res = await updateUserMutation({
      variables: {
        id: this.props.id,
        ...this.state,
      },
    })
    // Router.push({
    //   pathname: '/schedule',
    // })
  }

  render() {
    return (
      <Inner>
        <User>
          <Query query={CURRENT_USER_QUERY}>
            {({ data: { me }, loading }) => {
              if (loading) return <p>Loading...</p>
              if (!me) return <p>No User Found for ID {me.id}</p>
              return (
                <Mutation
                  mutation={UPDATE_USER_MUTATION}
                  variables={this.state}
                >
                  {(updateUser, { loading, error }) => (
                    <Form onSubmit={e => this.updateUser(e, updateUser)}>
                      <Error error={error} />
                      <fieldset disabled={loading} aria-busy={loading}>
                        <label htmlFor="firstName">
                          Account Manager's Name
                          <input
                            type="text"
                            id="name"
                            name="name"
                            placeholder="Name"
                            required
                            defaultValue={me.name}
                            onChange={this.handleChange}
                          />
                        </label>
                        <label htmlFor="businessName">
                          Edit Display Name:
                          <input
                            type="text"
                            id="businessName"
                            name="businessName"
                            placeholder="Business Display Name"
                            required
                            defaultValue={me.businessName}
                            onChange={this.handleChange}
                          />
                        </label>
                        <label htmlFor="email">
                          Login/Contact Email Address
                          <input
                            type="text"
                            id="email"
                            name="email"
                            placeholder="Email Address"
                            required
                            defaultValue={me.email}
                            onChange={this.handleChange}
                          />
                        </label>

                        <SickButton type="submit">
                          Sav{loading ? 'ing' : 'e'} Changes
                        </SickButton>
                      </fieldset>
                    </Form>
                  )}
                </Mutation>
              )
            }}
          </Query>
        </User>
      </Inner>
    )
  }
}

export default UpdateUserProfile
export { UPDATE_USER_MUTATION }
